<template>
  <div class="container pt-3">
                <h1 class="text-center">Sistema de Votaciones con VueJs</h1>
                
                <div class="container bg-info">
                    <h3>Añadir Personas</h3>
                    <input class="m-2 col-sm-12 col-md-3" type="text" id="nombre" placeholder="Nombre" v-model="inputNombre">
                    <input class="m-2 col-sm-12 col-md-3" type="text" id="apellido" placeholder="Apellido" v-model="inputApellido">
                    <button class="m-2 col-sm-12 col-md-3 bg-primary text-white" v-on:click="nuevaPersona">Agregar Persona</button>
                    
                </div>
                
                <div class="card m-3 pr-3 bg-warning" v-for="(i,index) in lista" :key="index">
                       <div class="row ">
                            <h4 class="p-1 ml-3 col"> {{i.nombre}} {{i.apellido}}</h4>
                            <button class="bg-success m-2" v-on:click="votar(index)">Votar</button>
                            <button class="bg-danger m-2" v-on:click="eliminar(index)">Eliminar</button>
                            
                        </div>
                        <div>
                            <h1 class="p-2 text-white">{{i.puntos}}</h1>
                        </div>
                </div>
                
              </div>
</template>

<script>
export default {
  name: 'Todo',
  data() {
      return {
        lista:[
        {
            nombre:'Sebastian',
            apellido:'Nieto',
            puntos:5
        },
        {
            nombre:'Patricia',
            apellido:'Ramirez',
            puntos:6
        }
    ],
    inputNombre:'',
    inputApellido:'',
      }
  },

  methods: {
      nuevaPersona(){
        if(this.inputNombre!='' && this.inputApellido!=''){
        this.lista.push({
            nombre:this.inputNombre,
            apellido:this.inputApellido,
            puntos:0
            }),
            this.lista.sort(function(a,b){
          return(b.puntos-a.puntos)
        }),
        document.getElementById("nombre").value = "";
        document.getElementById("apellido").value = "";
        this.inputNombre='',
        this.inputApellido=''}

      },


      eliminar(index){
        this.lista.splice(index,1)
        this.lista.sort(function(a,b){
          return(b.puntos-a.puntos)
        })
      },


      votar(index){
        this.lista[index].puntos++;
        this.lista.sort(function(a,b){
          return(b.puntos-a.puntos)
        })
      }
  },
}
</script>



